.divPokeEvo {
  width: 95%;
  border-radius: 2%;
  padding: 1rem 0;
  margin: 0 auto;
  margin-bottom: 2rem;
}

.grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-flow: row dense;
  grid-gap: 0.5rem;
}
.equisde {
  margin: 0 auto;
  width: 90%;
  background-color: white;
  align-items: center;
  text-align: center;
  cursor: pointer;
  padding-bottom: 1rem;
}

.pokeName {
  width: max-content;
  text-align: center;
  font-weight: bold;
  color: #fff;
  padding: 0.25rem;
  border: 1px solid #fff;
  border-radius: 10%;
  cursor: pointer;
  margin: 0 auto;
}

@media screen and (min-width: 48rem) {
  .divPokeEvo {
    margin: 0 auto;
  }
}
