@media screen and (min-width: 48rem) {
  .CSS {
    display: grid;
    min-height: 100vh;
    min-width: 100%;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(10, auto);
  }

  .Hashtag {
    grid-column: 1 / span 2;
    grid-row: 1;
  }
  .Imagen {
    grid-column: 1;
    grid-row: 2;
  }
  .Card {
    grid-column: 2;
    grid-row: 2 / 5;
  }
  .BaseStatus {
    grid-column: 1;
    grid-row: 3 / 5;
  }
  .Abilities {
    grid-column: 2;
    grid-row: 4 / 10;
    margin-top: 1rem;
  }
  .Evolution {
    grid-column: 1;
    grid-row: 5/10;
    margin-top: 1rem;
  }
  /* IMG - NO EVO */
  .Imagen2 {
    grid-column: 1;
    grid-row: 2;
  }
  .Card2 {
    grid-column: 2;
    grid-row: 2 / 4;
  }
  .BaseStatus2 {
    grid-column: 1;
    grid-row: 3/5;
  }
  .Abilities2 {
    grid-column: 2;
    grid-row: 4 / 10;
  }
}
@media screen and (min-width: 64rem) {
  .CSS {
    display: grid;
    min-height: 100vh;
    min-width: 100%;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(10, auto);
  }
  .Hashtag {
    grid-column: 1 / span 5;
    grid-row: 1;
  }
  .Imagen {
    grid-column: 2 / span 2;
    grid-row: 2/4;
  }
  .Card {
    grid-column: 1;
    grid-row: 2/10;
  }
  .BaseStatus {
    grid-column: 4;
    grid-row: 2/10;
  }
  .Abilities {
    grid-column: 5;
    grid-row: auto;
    margin: 0;
  }
  .Evolution {
    grid-column: 5;
    grid-row: auto;
  }

  /* IMG - NO EVO */
  .Imagen2 {
    grid-column: 2 / span 2;
    grid-row: 2/4;
  }
  .Card2 {
    grid-column: 1;
    grid-row: 2/10;
  }
  .BaseStatus2 {
    grid-column: 4;
    grid-row: 2/10;
  }
  .Abilities2 {
    grid-column: 5;
    grid-row: auto;
    margin: 0;
  }
}
