.navBar {
  width: 100%;
  height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav {
  width: 100%;
}
.navFlex {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  gap: 0.25rem;
}
.inputSearch {
  height: 2rem;
  width: 12rem;
  border: none;
  border: 1px solid #000;
  border-radius: 5rem;
  padding-left: 1rem;
}
.btn {
  height: 2rem;
  border-radius: 15%;
  color: #fff;
  border: none;
  cursor: pointer;
}
.btnSearch {
  background-color: #27ae60;
  border: 1px solid #27ae6040;
}

@media screen and (max-width: 350px) {
  .inputSearch {
    width: 10rem;
  }
}
@media screen and (min-width: 48rem) {
  .navFlex {
    justify-content: flex-start;
    padding: 1rem;
    gap: 1rem;
  }
  .inputSearch {
    width: 16rem;
  }
  .btn {
    width: 6rem;
    border-radius: 10%;
  }
  .btnRandom {
    align-self: flex-end;
    position: absolute;
    left: 80%;
  }
}
@media screen and (min-width: 64rem) {
  .navFlex {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
  }
  .btn {
    border: 2%;
    height: 3rem;
    font-size: 1.15rem;
    font-weight: bold;
  }
  .inputSearch {
    font-size: 1.5rem;
    width: 22rem;
    height: 3rem;
  }
  .btnRandom {
    position: inherit;
  }
}
