.statsPoke {
  width: 95%;
  height: max-content;
  margin: 0 auto;
  background-color: #bfc9ca60;
  border-radius: 2%;
  margin-top: 1rem;
  margin-bottom: 1rem;
  padding: 0.5rem 1rem;
}
.statsPoke h2 {
  text-align: center;
}
.container {
  width: 100%;
  background-color: #bfc9ca;
  border-radius: 45rem;
}
.stat {
  text-align: right;
  border-radius: 45rem;
}
.nameStat {
  filter: invert(1);
  font-weight: bold;
  padding: 0.2rem;
  padding-right: 0.5rem;
}

