.byme {
  position: absolute;
  margin-top: 1rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: white;
  width: 100%;
  height: 3rem;
  text-align: center;
  gap: 0.25rem;
}
.byme a {
  text-decoration: none;
  font-weight: bold;
  margin-right: 1rem;
}
.byme a:visited {
  color: white;
}
