.ability {
  width: 95%;
  border-radius: 2%;
  margin: 1rem auto;
  height: max-content;
  min-height: 5rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.abilityDiv {
  width: 90%;
  margin: 0 auto;
  border-radius: 2%;
  background-color: #fff;
}
.abilityDiv h3 {
  margin: 0;
  padding: 1rem;
}
.abilitiesMap {
  height: max-content;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-flow: row dense;
  grid-gap: 0.5rem;
  padding: 0.5rem;
}
.abilityMap {
  height: max-content;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0.5rem;
}
.abilityBtn {
  width: max-content;
  text-align: center;
  font-weight: bold;
  color: #fff;
  padding: 0.25rem;
  border: 1px solid #fff;
  border-radius: 10%;
  cursor: pointer;
  margin: 0 auto;
}
.abilityDesc {
  width: 90%;
  border-radius: 2%;
  margin: 0 auto;
  max-height: max-content;
  background-color: #fff;
}
.abilityDesc p {
  margin-bottom: 0;
  padding: 1rem;
}
