.NoPokemonDiv {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: baseline;
  align-items: center;
}
.errorPokemon {
  width: 90%;
  height: auto;
  margin: 0 auto;
}
.errorPokeball {
  width: 50%;
  height: auto;
}
.NoPokemonDiv p {
  font-size: 1.15rem;
  font-weight: bold;
}
@media screen and (min-width: 64rem) {
  .NoPokemonDiv {
    height: 100vh;
  }
  .errorPokemon {
    width: 20%;
    height: auto;
    margin: 0 auto;
  }
  .errorPokeball {
    width: 10%;
  }
}
