.imgDivPoke {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #bfc9ca60;
  padding: 1rem;
  width: 95vw;
  height: 15rem;
  margin: 0 auto;
  border-radius: 2%;
  margin-bottom: 2rem;
}
.imgPoke {
  height: 100%;
  max-width: 100%;
  width: auto;
}
@media screen and (min-width: 48rem) {
  .imgDivPoke {
    width: 95%;
    margin: 0 auto;
    margin-bottom: 1rem;
    margin-top: 0.75rem;
  }
}
@media screen and (min-width: 64rem) {
  .imgDivPoke {
    width: 95%;
    height: 35rem;
  }
}
