.dataPoke {
  width: 95%;
  margin: 0 auto;
  height: max-content;
  border-radius: 2%;
  padding: 0 0 0.15rem 0;
}
.nickName {
  text-align: center;
  font-size: 1.75rem;
  margin: 0.75rem;
  padding: 0.75rem 0;
  color: #000;
  text-shadow: 0px 0px 1rem #fff;
}
.legendary {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.legendary i {
  background-color: #000;
  color: #eabe3f;
  padding: 0.5rem 1rem;
  border-radius: 45rem;
  font-weight: bold;
}
.divData {
  background: #fff;
  width: 95%;
  padding: 0.25rem 1rem;
  border-radius: 1rem;
  margin: 1rem auto;
  border: thin solid #bfc9ca;
}
.divData h4 {
  margin: 1rem 0;
  padding: 0;
}
.types {
  background: #fff;
  width: 95%;
  padding: 0.25rem 1rem;
  border-radius: 1rem;
  margin: 1rem auto;
  border: thin solid #bfc9ca;
}
.typesPoke {
  display: flex;
  justify-content: space-around;
}
.types h3 {
  margin-bottom: 0;
}
.typePoke {
  width: 5rem;
  text-align: center;
  font-weight: bold;
  color: #fff;
  padding: 0.25rem;
  border: 1px solid #fff;
  border-radius: 10%;
}

@media screen and (min-width: 48rem) {
  .dataPoke {
    margin: 0 auto;
    margin-top: 1;
  }
}
